import * as React from "react";
import { createRoot } from "react-dom/client";
import { createBrowserRouter, RouterProvider, Route, Link } from "react-router-dom";
import App from "./App";
import { AllData } from "./Data/AllData";
import AllContact from "./Contact/AllContact";
import AllDetails from "./ProductDetails/AllDetails";
import AllExplore from "./Explore/AllExplore";
import AllShop from "./Shop/AllShop";
import AllCart from "./Cart/AllCart";
import ScrollToTop from "react-scroll-to-top";


const router = createBrowserRouter([
  {
    path: "/",
    element: <App />
  },

  {
    path: "contact",
    element: <AllContact />
  },

  {
    path: "details",
    element: <AllDetails />
  },

  {
    path: "explore",
    element: <AllExplore />
  },

  {
    path: "shop",
    element: <AllShop />
  },

  {
    path: "cart",
    element: <AllCart />
  },

]);

createRoot(document.getElementById("root")).render(
  <AllData>
    <RouterProvider router={router} />
    <ScrollToTop smooth
    svgPath="M256 512A256 256 0 1 0 256 0a256 256 0 1 0 0 512z
    M385 215c9.4 9.4 9.4 24.6 0 33.9s-24.6 9.4-33.9 0l-71-71L280 392c0 13.3
    -10.7 24-24 24s-24-10.7-24-24l0-214.1-71 71c-9.4 9.4-24.6 9.4-33.9 0s-9.4-24.6 0-33.9L239 103c9.4-9.4 24.6-9.4 33.9 0L385 215z"
    viewBox="0 0 512 512"
    color="#007cf8" style={{ backgroundColor: "#fff", borderRadius: "50px", boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)" }}
    />
  </AllData>
);
